:root{
  --setDisplayPermissionGranted: none;
  --setDisplayStart: flex;
  --hideUI: none;
}

.startElement{
  dispay: var(--setDisplayStart);
}

.hiddenUI{
  display: var(--hideUI)
}

#startDiv{
  width: 100%;
  height: 100%;
  margin: 0%;
  background-color: #282c34;
  padding-top: 20%;
  padding-bottom: 70%;
}
#playButton{
  display: flex;
  margin: auto;
  background-color: #202221a3;
  /*font-size: calc(20px + 3vmin);*/
  margin-top: 5%;
}

.p{
  color: blue
}

.pPulsatingText{
  -webkit-animation: pulsate 3s ease-out;
  -webkit-animation-iteration-count: infinite; 
  opacity: 0.5;
}
.PulsatingText {
  /* Chrome, Safari, Opera */
 -webkit-animation: PULSE 1s infinite; 
 
 /* Internet Explorer */
 -ms-animation: PULSE 1s infinite;
 
 /* Standard Syntax */
 animation: PULSE 1.25s infinite; 
}
/* Chrome, Safari, Opera */
@-webkit-keyframes PulsatingText{
  0%{color:rgba(255,0,255);}	
 110%{color: black;}
}

@-webkit-keyframes PppulsatingText {
  0% { 
      opacity: 0.5;
  }
  50% { 
      opacity: 1.0;
  }
  100% { 
      opacity: 0.5;
  }
}
@keyframes PulsatingText{
  0%{color:rgba(0,255,255);}	
	110%{color: black;}
}
@-ms-keyframes PulsatingText{
  0%{color:rgba(255,255,0);}	
	110%{color: black;}
}

#titleGame{
  /*color: white;*/
  font-size: 13px;
  padding: 0px;
  padding-bottom:30%;
  margin-top: 0%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: var(--setDisplayPermissionGranted);
  background-color: #282c34;
  min-height: 100vh;
  /*display: flex;*/
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.colDiv{
  flex-grow: 1;
  text-align: center;
  margin-left: 40px;
  margin-right: 40px;
}
.rowDiv{
  display: flex;
}
